import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ThietBiModel } from '../../../../../shared/models/thiet-bi.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { MuonThietBiService } from './muon-thiet-bi.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatChoComponent } from './dat-cho/dat-cho.component';
import { GiaoVienModel } from '../../../../../shared/models/giao-vien.model';
import { environment, eMenuClient } from 'src/environments/environment';
import { MainNavComponent } from '../../main.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-muon-thiet-bi',
  templateUrl: './muon-thiet-bi.component.html',
  styleUrls: ['./muon-thiet-bi.component.scss'],
})
export class MuonThietBiComponent implements OnInit {
  _length = 0;
  //đúng tên thuộc tính để có thể sort
  displayedColumns: string[] = [
    'MaThietBiChiTiet',
    'Ten',
    'TenTinhTrang',
    'NamSuDung',
    'TenNoiQuanLy',
    'TenNuocSanXuat',
    'SuDungToiDa',
    'SoLuongDangSuDung',
    'actions',
  ];
  showProgressBar = false;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    private __service: MuonThietBiService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainNavComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.CLIENT,
      eMenuClient[eMenuClient.MUON_THIET_BI]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.CLIENT
    );
  }
  _giaoViens: GiaoVienModel[];
  _thietBis: ThietBiModel[];
  dataSource: MatTableDataSource<ThietBiModel>;
  ngOnInit(): void {
    this.showProgressBar = true;
    this.getDataThietBi();
    this.getDataGiaoVien();
  }
  delay(ms: number) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  private getDataThietBi() {
    this.showProgressBar = true;
    this.__service.getAllThietBiAvailable().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._thietBis = data;
        this.dataSource = new MatTableDataSource(this._thietBis);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: ThietBiModel,
          filter: string
        ) =>
          data.Ma.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.Ten.toLocaleLowerCase().indexOf(filter) != -1;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  private getDataGiaoVien() {
    this.showProgressBar = true;
    this.__service.getAllGiaoVien().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._giaoViens = data;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  register(thietBi: ThietBiModel) {
    const confirmDialogRef = this.dialog.open(DatChoComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        thietBi: thietBi,
        giaoViens: this._giaoViens,
      },
    });
    confirmDialogRef.afterClosed().subscribe((res) => {
      if (res.result == true) {
        this.showProgressBar = true;
        this.getDataThietBi();
      }
    });
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
