import { Component, OnInit, Inject } from '@angular/core';
import { LichSuDatChoRequestModel } from '../../../../../../shared/models/lich-su-dat-cho.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { DatChoService } from './dat-cho.service';
import { AlertDialogComponent } from '../../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import {
  ThietBiModel,
  ThietBiReqModel,
} from '../../../../../../shared/models/thiet-bi.model';
import { GiaoVienModel } from '../../../../../../shared/models/giao-vien.model';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { DatePipe } from '@angular/common';
import { ThietBiChiTietModel } from '../../../../../../shared/models/thiet-bi-chi-tiet.model';
import { MatSelectChange } from '@angular/material/select';
import { FormGroup, FormControl } from '@angular/forms';

import { DateAdapter, MAT_DATE_FORMATS } from '@angular/material/core';
import {
  AppDateAdapter,
  APP_DATE_FORMATS,
} from '../../../../../../shared/tools/AppDateAdapter';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
@Component({
  selector: 'app-dat-cho',
  templateUrl: './dat-cho.component.html',
  styleUrls: ['./dat-cho.component.scss'],
  providers: [
    { provide: DateAdapter, useClass: AppDateAdapter },
    { provide: MAT_DATE_FORMATS, useValue: APP_DATE_FORMATS },
  ],
})
export class DatChoComponent implements OnInit {
  _datCho = new LichSuDatChoRequestModel();
  _thietBi = new ThietBiReqModel('', '', 1);
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<DatChoComponent>,
    private __service: DatChoService,
    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar,
    public datepipe: DatePipe,
    private __router: Router
  ) {}
  _title: string;
  _titleMainButton: string;
  _minDate = new Date();
  _maxDate = new Date();
  _giaoViens: GiaoVienModel[];
  _hasValueStartDay = false;
  _hasValueEndDay = false;
  showProgressBar = false;
  __thietBiChiTiets: ThietBiChiTietModel[];
  _selectedThietBiChiTiet: ThietBiChiTietModel;
  campaignTwo: FormGroup;

  myFilter = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    // Prevent Saturday and Sunday from being selected.
    return day !== 0 && day !== 6;
  };

  ngOnInit(): void {
    // console.log(this.data)
    this._title = 'Đăng ký mượn thiết bị';
    this._titleMainButton = 'Đăng ký';
    this._maxDate.setDate(this._minDate.getDate() + 30);

    if (this.data.thietBi) {
      this._thietBi = this.data.thietBi;
      this._datCho.MaThietBiChiTiet = this._thietBi.IDThietBiChiTiet;
    }
    if (this.data.giaoViens) {
      this._giaoViens = this.data.giaoViens;
    }
    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date()),
      end: new FormControl(new Date()),
    });
  }
  selectThietBiChiTietChange(event: MatSelectChange) {
    // this.fromInput.value = '';
    // this.toInput.value = '';
    this._selectedThietBiChiTiet = this.__thietBiChiTiets.find(
      (f) => f.Ma == event.value
    ) as ThietBiChiTietModel;
    if (
      this._selectedThietBiChiTiet.NgayBatDau &&
      this._selectedThietBiChiTiet.NgayKetThuc
    ) {
      this.myFilter = (d: Date | null): boolean => {
        const date = (d || new Date()).getDate();
        const ngayBatDau = new Date(
          this._selectedThietBiChiTiet.NgayBatDau
        ).getDate();
        const ngayKetThuc = new Date(
          this._selectedThietBiChiTiet.NgayKetThuc
        ).getDate();
        // Prevent Saturday and Sunday from being selected.

        return date < ngayBatDau || date > ngayKetThuc;
      };
      this.campaignTwo = new FormGroup({
        start: new FormControl(
          new Date(this._selectedThietBiChiTiet.NgayBatDau)
        ),
        end: new FormControl(
          new Date(this._selectedThietBiChiTiet.NgayKetThuc)
        ),
      });
    } else {
      this.myFilter = (d: Date | null): boolean => {
        return true;
      };
      //TODO: clear comparasion
      this.campaignTwo = new FormGroup({
        start: new FormControl(new Date(1000, 1, 1)),
        end: new FormControl(new Date(1000, 1, 1)),
      });
    }

    // this._datCho.NgayBatDau = ""
    // this._datCho.NgayKetThuc = ""
    // this._hasValueStartDay = false
    // this._hasValueEndDay = false
  }
  addEventNgayBatDau(type: string, event: MatDatepickerInputEvent<Date>) {
    this._datCho.NgayBatDau = this.datepipe.transform(event.value, 'short');
    this._hasValueStartDay = true;
  }
  addEventNgayKetThuc(type: string, event: MatDatepickerInputEvent<Date>) {
    this._datCho.NgayKetThuc = this.datepipe.transform(event.value, 'short');
    this._hasValueEndDay = true;
    const datChoNgayBatDau = new Date(this._datCho.NgayBatDau).getDate();
    if (
      this._selectedThietBiChiTiet.NgayBatDau &&
      this._selectedThietBiChiTiet.NgayKetThuc
    ) {
      const ngayBatDau = new Date(
        this._selectedThietBiChiTiet.NgayBatDau
      ).getDate();
      if (datChoNgayBatDau < ngayBatDau) {
        const ngayKetThuc = new Date(
          this._selectedThietBiChiTiet.NgayKetThuc
        ).getDate();
        const datChoNgayKetThuc = new Date(this._datCho.NgayKetThuc).getDate();
        if (datChoNgayKetThuc > ngayKetThuc) {
          //không được phép đặt chỗ như vậy
          // this.fromInput.value = '';
          // this.toInput.value = '';
          this.__snackbar.open('Vui lòng chọn ngày hợp lệ', null, {
            duration: 2000,
          });
        }
      }
    }
  }
  register() {
    if (
      this._datCho.MaGiaoVien == '' ||
      !this._hasValueStartDay ||
      !this._hasValueEndDay
    ) {
      this.__snackbar.open('Vui lòng nhập đủ thông tin', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this.__service.addLichSuDatCho(this._datCho).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Đăng ký thành công', null, {
              duration: 2000,
            });
            let res = this._datCho;
            this.dialogMainRef.close({
              result: true,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },
        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._thietBi,
          result: false,
        });
      }
    });
  }
}
