import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../../environments/environment';
import { LichSuDatChoRequestModel } from '../../../../../../shared/models/lich-su-dat-cho.model';

@Injectable({
  providedIn: 'root',
})
export class DatChoService {
  constructor(private httpClient: HttpClient) {}

  getAllThietBiAvailable(): Observable<any> {
    let url = environment.api.url + 'ThietBi/getAllAvailable';
    return this.httpClient.get(url);
  }

  //--------------------------------
  getAllGiaoVien(): Observable<any> {
    let url = environment.api.url + 'GiaoVien/getAll';
    return this.httpClient.get(url);
  }
  //------------------------------
  addLichSuDatCho(lichSuDatCho: LichSuDatChoRequestModel): Observable<any> {
    let url = environment.api.url + 'LichSuDatCho/add';
    return this.httpClient.post(url, lichSuDatCho);
  }
  //--------------------------
  getThietBiChiTiets(maThietBi: string): Observable<any> {
    let url = environment.api.url + 'ThietBiChiTiet/get?MaThietBi=' + maThietBi;
    return this.httpClient.get(url);
  }
}
